@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
:root {
    --primaryColor: #61DAFB;
    --secondaryColor: #282C34;
}

*{
    margin: 0;
    padding: 0;
}

body{
    font-family: 'Open Sans', sans-serif;
}

.container{
    width: 1140px;
	padding: 0 15px;
	margin: 0 auto;
}

.flexCenter{
    min-height: calc(100vh - 86px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    text-align: center;
}

.centerCenter{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.button{
    font-size: 20px;
    border: none;
    padding: 10px 20px;
    margin-top: 20px;
    cursor: pointer;
    text-decoration: none;
}

.buttonRadius{
    font-size: 14px;
    border-radius: 100px;
}
#header{
    background-color: var(--secondaryColor);
    padding: 10px 0;
}
#header .container{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#header .logo a{
    text-decoration: none;
    color: #fff;
    font-size: 30px;
}

#header nav ul{
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
#header nav ul li{
    color: #fff;
    padding: 5px;
    margin: 5px;
    position: relative;
    transition: all .5s ease;
}
#header nav ul li a{
    text-decoration: none;
    font-size: 15px;
    color: #fff;
    padding: 2px 0;
    display: inline-block;
    border-bottom: 2px solid transparent;
    transition: all .5s ease;
}
#header nav ul li:hover > a, #header nav ul li .active{
    color: var(--primaryColor);
    border-bottom: 2px solid var(--primaryColor);
}
#header nav ul li ul{
    list-style-type: none;
    position: absolute;
    min-width: 150px;
    width: -webkit-max-content;
    width: max-content;
    padding: 10px;
    margin-top: 5px;
    border-radius: 3px;
    background-color: var(--primaryColor);
    box-shadow: 0 5px 10px 0 rgba(#000, .2);
    left: -9999px;
    z-index: 10;
}
#header nav ul li:hover ul{
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: block;
}
#header nav ul li ul li{
    padding: 7px;
}
#header nav ul li ul li a{
    color: #333;
}
#header nav ul li ul li:hover > a, #header nav ul li ul li .active{
    color: #fff;
    border-bottom: 2px solid #fff;
}

ul .menu-item-has-children >a::after{
    content: "";
    display: inline-block;
    width: 0;
    height: 0;
    margin-left: 3.5px;
    vertical-align: 1.5px;
    border-top: 7px solid;
    border-right: 6.5px solid transparent;
    border-bottom: 0;
    border-left: 6.5px solid transparent;
}
